<template>
    <div>
        <sub-controller
                v-if="clone.wrench"
                :image="'wrench.png'"
                @click.prevent.stop="handleClick($event)"
        >
        </sub-controller>
        <vue-simple-context-menu
                :elementId="'myUniqueId'"
                :options="options"
                :ref="'vueSimpleContextMenu'"
                @option-clicked="optionClicked"
        />
        <!--sub-controller
          v-if="clone.deleteActivity"
          :image="'trash.png'"
          v-on:click="deleteActivity">
        </sub-controller-->
    </div>

</template>

<script>
    export default {
        name: 'storming-sub-controller',
        props: {
            type: String,
            className: String,
            calleeDefinitionId: String
        },
        computed: {},
        data: function () {
            return {
                clone: {
                    gateway: false,
                    wrench: false,
                },
                options: [
                    {
                        name: 'IDE',
                    }
                ]
            }
        },
        watch: {},
        mounted: function () {
            this.clone.wrench = true;
        },
        /**
         * clone : 컨트롤러에 의해 신규 bpmn 이 생성되었을 경우
         * showComponentChange : 컨트롤러중 렌치 모양을 클릭하여 도형 변경 창을 여는 경우
         */
        methods: {
            end() {

            },
            handleClick(event) {
                console.log(event)
                event.pageY = event.pageY - 62;
                this.$refs.vueSimpleContextMenu.showMenu(event)
            },
            optionClicked(event) {
                var me = this
                console.log(me.className);
                console.log(me)
                if (event.option.name == 'Get Pods') {
                    me.getPods();
                } else if (event.option.name == 'IDE') {
                    me.ide();
                } else if (event.option.name == 'Serivce') {
                    me.service();
                }
            },
            getPods() {
                $('iframe').get(0).contentWindow.wt.term.term.send("kubectl get po \r")
            },
            ide() {
                var me = this

                    me.$EventBus.$once('returnDeployData', function (data) {
                        var deployFileKey, deployFileCode;
                        console.log(data)
                        data.fullPathList.forEach(function (itemKey) {
                            if (itemKey.fullPath.includes('kubernetes/deployment')) {
                                deployFileKey = itemKey.key
                            }
                        })

                        data.getCodeList.forEach(function (itemCode) {
                            if (itemCode.key == deployFileKey) {
                                deployFileCode = itemCode.code
                            }
                        })

                        // console.log(deployFileCode);
                    })
                    me.$EventBus.$emit('deploy', me.$parent.$parent.value.elementView.id);

            },
            service() {
                var me = this
                console.log(me.$parent.$parent.value.elementView.id)
                me.$EventBus.$once('returnDeployData', function (data) {
                    var serviceFileKey, serviceFileCode;
                    data.fullPathList.forEach(function (itemKey) {
                        if (itemKey.fullPath.includes('kubernetes/service')) {
                            serviceFileKey = itemKey.key
                        }
                    })
                    data.getCodeList.forEach(function (itemCode) {
                        if (itemCode.key == serviceFileKey) {
                            serviceFileCode = itemCode.code
                        }
                    })
                    console.log(serviceFileCode);
                })
                me.$EventBus.$emit('service', me.$parent.$parent.value.elementView.id);
            },
            // service() {
            //     var me = this
            //     console.log(me.$parent.$parent.value.elementView.id)
            //     me.$EventBus.$once('returnDeployData', function (data) {
            //         var serviceFileKey, serviceFileCode;
            //         data.fullPathList.forEach(function (itemKey) {
            //             if (itemKey.fullPath.includes('kubernetes/service')) {
            //                 serviceFileKey = itemKey.key
            //             }
            //         })
            //         data.getCodeList.forEach(function (itemCode) {
            //             if (itemCode.key == serviceFileKey) {
            //                 serviceFileCode = itemCode.code
            //             }
            //         })
            //         console.log(serviceFileCode);
            //     })
            //     me.$EventBus.$emit('service', me.$parent.$parent.value.elementView.id);
            // },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },
        }
    }
</script>


