<script>
    // import Moveable from 'vue-moveable';
    import TextReader from "@/components/yaml.vue";
    import CodeViewer from "../CodeViewer";
    import {
        saveAs
    } from 'file-saver';
    import firebase from 'firebase'
    import {
        mdiAbTesting
    } from '@mdi/js';
    import * as io from 'socket.io-client'


    var fs = require('fs');
    var _ = require('lodash');
    var Minio = require('minio');
    var Base64 = require('js-base64').Base64;
    var yamlpaser = require('js-yaml');
    var FileSaver = require('file-saver');
    var changeCase = require('change-case');
    var pluralize = require('pluralize');
    var JSZip = require('jszip')
    var path = require('path')
    import {diffString, diff} from "json-diff";

    // var deepDiff = require('deep-diff').diff;
    // var observableDiff = require('deep-diff').observableDiff;
    // var applyDiff = require('deep-diff').applyDiff;
    // var applyChange = require('deep-diff').applyChange;
    // var revertChange = require('deep-diff').revertChange;
    var jsondiffpatch = require('jsondiffpatch')

    export default {
        name: 'modeling-designer',
        components: {
            TextReader,
            saveAs,
            CodeViewer,
            mdiAbTesting,
            diffString, diff,
            // Moveable,
            io
        },
        props: {
            elementTypes: Array
        },
        data() {
            return {
                // moveableOption: {
                //     draggable: true,
                //     throttleDrag: 0,
                //     resizable: false,
                //     throttleResize: 1,
                //     keepRatio: true,
                //     scalable: true,
                //     throttleScale: 0,
                //     rotatable: false,
                //     throttleRotate: 0,
                // },
                //db
                isShare: false,
                loadingDialog: true,
                codeViewing: false,
                saveItems: [
                    {title: 'Save to Server'},
                    {title: 'Download model File'}
                ],
                openItems: [
                    {title: 'Open from Files'},
                    // {title: 'open from Local'},
                ],
                codeItems: [
                    {title: 'Code Preview'},
                    {title: 'Download Archive'},
                ],
                onlineSaveDialog: false,
                onlineSaveComfirmDialog: false,
                isLoadVersion: false,
                //server
                author: '', //uid
                serverComment: '',
                serverDate: '',
                editing: true,
                files: {
                    version: 'mdi-server',
                    code: 'mdi-code-array',
                    save: 'mdi-content-save',
                    open: 'mdi-book-open',
                    md: 'mdi-markdown',
                    txt: 'mdi-file-document-outline',
                    java: 'mdi-language-java',
                    xml: 'mdi-xml',
                    shell: 'mdi-powershell',
                    docker: 'mdi-docker',
                    png: 'mdi-file-image',
                    json: 'mdi-json',
                    python: 'mdi-language-python',
                    fork: ' mdi-silverware-fork'
                },
                canvas: null,
                dragPageMovable: false,
                value: {
                    'definition': [],
                    'relation': []
                },
                projectName: '',
                generateZipDialog: false,
                //undo Redo
                undoRedoStorage: 20,
                undoRedoArray: [],
                undoRedoIndex: 0,
                undoRedoLastIndex: 0,
                imageBase: 'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/symbol/',
                //스낵바 옵션
                snackbar: false,
                color: 'error',
                mode: 'multi-line',
                timeout: 6000,
                text: '수정중입니다.',
                //Zip
                pathTmp: [],
                gitCodeUrl: {},
                //data structure
                model: [],
                fullPathList: [],
                treeList: [],
                getCodeList: [],
                openCode: [],
                template: '',
                treeOpen: true,
                gitUrl: '',
                gitPath: [],
                gitCode: [],
                checkName: true,
                // Children
                drawer: false,
                chatUid: '',
                messageRef: {},
                //version
                versionList: [],
                version: '',
                revisionInfo: {},
                showVersionsDialog: false,
                dialog2: false,
                kubernetesToken: '',
                clusterAddress: '',
                tokenDialog: false,
                parmVersion: '',
                parmType: '',
                parmProjectId: '',
                parmUid: '',

                //save projectId
                newProjectId: '',
                //view
                viewSelect: {},
                viewOpenId: '',
                // viewRowAddDialog: false,
                viewSelectType: 'cqrs',
                entityActionList: ['create', 'update', 'delete'],
                entityAction: '',
                //view create
                viewColumnTypeList: ['String', 'Integer', 'Double'],
                selectEvent: [],
                viewColumnType: '',
                viewColumn: '',
                sourceEventColumn: '',
                defaultValue: '',
                isRead: true,
                //save
                ver1: false,
                ver2: false,
                ver3: false,
                //fork
                forkDialog: false,
                undoRedoInitValue: {},
                isUndoRedo: false,
                connetServer: '',
                roomId: "masez",
                // img: null,
                rtcLogin: false,
                webrtcDialog: false,

                //multi Edit
                // isYourValueChange: true,
                database: null,

                isSelfEdit: true,

            }
        },
        beforeDestroy: function () {
            var me = this

            //rtc
            me.onLeave()

            me.database
                .ref("/saver/sharedMulti/" + me.parmProjectId + "/" + me.parmVersion + '/participants')
                .child(localStorage.getItem('uid'))
                .remove()

            //recevie off
            me.database.ref('value').off()

            //일정 시간 마다 가져오는 함수 멈추기
            clearInterval(me.connetServer);

            localStorage.removeItem('projectId')
            localStorage.removeItem('sharedMultiTimeStamp')
            // me.$EventBus.$emit('showSave')

            // console.log(me.ver1, me.ver2)
            if (me.ver1) {
                me.$EventBus.$emit('saveComplete')
            }
            me.$refs['modeler-image-generator'].save(me.id, me.canvas).then(function (resolve) {

                if (me.parmType == '0') {
                    localStorage.setItem('localSaveImg', resolve)
                } else if (me.parmType == 'true') {
                    //share
                    me.database.ref('saver/shared/' + me.parmUid + "/" + me.parmProjectId + "/" + me.parmVersion + '/')
                        .update({
                            img: resolve
                        });
                } else if (me.parmType == 'false') {
                    //mine
                    me.database.ref('saver/mine/' + me.parmUid + "/" + me.parmProjectId + "/" + me.parmVersion + '/')
                        .update({
                            img: resolve
                        }, function (err) {
                            if (err) {

                            } else {
                                // me.$EventBus.$emit('saveComplete')
                            }
                        });
                }
                me.$EventBus.$emit('updatePic', resolve)

            })


            // me.canvas.removeSlider();
            // me.$EventBus.$emit('closeChat')
            // me.messageRef.off();


        },
        computed: {
            checkValueRelation() {
                // return  jsondiffpatch.clone(this.value)
                return _.cloneDeep(this.value)
            },
            eventList: function () {
                var me = this
                var tmp = []
                me.value.definition.forEach(function (tmpData) {
                    if (tmpData != null) {
                        if (tmpData._type == 'org.uengine.modeling.model.Event')
                            tmp.push(tmpData)
                    }

                })

                tmp = tmp.filter(n => n)
                return tmp
            },
            templateList: function () {
                var me = this
                return Object.keys(me.$manifestsPerTemplate)
            },
            id: {
                get: function () {
                    return this.projectName
                }
            },
        },
        created: function () {
            var me = this
            //database db setting
            me.database = firebase.database()
            window.io = io

            me.$EventBus.$emit('progressValue', true)
            me.parmUid = this.$route.params.userUid;
            me.parmProjectId = this.$route.params.projectId;
            me.parmVersion = this.$route.params.projectVersion;
            me.parmType = this.$route.params.type;
            me.roomId = 'msaez_' + me.parmProjectId


            localStorage.setItem('queueLastIndex', '0')

            var valueInit = {"definition": [], "relation": []}

            var userUid = localStorage.getItem('uid')

            // me.$nextTick(function () {

            if (me.parmType == '0') {
                me.projectName = localStorage.getItem('localSaveProjectName') != null ? localStorage.getItem('localSaveProjectName') : 'local'

                if (localStorage.getItem('localLoadData') != null) {
                    me.value = JSON.parse(localStorage.getItem('localLoadData'));
                    me.$nextTick(function () {
                        me.$EventBus.$emit('progressValue', false)
                    })

                } else {
                    me.value = valueInit
                    me.$nextTick(function () {
                        me.$EventBus.$emit('progressValue', false)
                    })


                }
            } else if (me.parmType == '2') {
                localStorage.setItem('projectId', me.parmProjectId)

                me.database
                    .ref("/saver/sharedMulti/" + me.parmProjectId + "/" + me.parmVersion + '/participants')
                    .child(localStorage.getItem('uid'))
                    .update({
                        'uid': localStorage.getItem('uid'),
