            bindEvents: function (opengraph) {
                var me = this;
                var el = me.$el;
                var canvasEl = $(opengraph.container);
                if (!canvasEl || !canvasEl.length) {
                    return;
                }
                this.canvas = opengraph.canvas;
                //아이콘 드래그 드랍 이벤트 등록
                $(el).find('.draggable').draggable({
                    start: function () {
                        canvasEl.data('DRAG_SHAPE', {
                            'component': $(this).attr('_component'),
                            'width': $(this).attr('_width'),
                            'height': $(this).attr('_height')
                        });
                    },
                    helper: 'clone',
                    appendTo: canvasEl
                });

                canvasEl.droppable({
                    drop: function (event, ui) {
                        var componentInfo = canvasEl.data('DRAG_SHAPE'),
                            shape, element;
                        if (componentInfo) {
                            var dropX = event.pageX - canvasEl.offset().left + canvasEl[0].scrollLeft;
                            var dropY = event.pageY - canvasEl.offset().top + canvasEl[0].scrollTop;

                            dropX = dropX / opengraph.scale;
                            dropY = dropY / opengraph.scale;

                            componentInfo = {
                                component: componentInfo.component,
                                x: dropX,
                                y: dropY,
                                width: parseInt(componentInfo.width, 10),
                                height: parseInt(componentInfo.height, 10),
                                label: ''
                            }
                            me.addElement(componentInfo);
                        }
                        canvasEl.removeData('DRAG_SHAPE');
                    }
                });
            },
            dbuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + s4() + s4() + s4() + s4() + s4() + s4();
            },
            uuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            },
            onConnectShape: function (edge, from, to) {

                var me = this;
                //존재하는 릴레이션인 경우 (뷰 컴포넌트), 데이터 매핑에 의해 자동으로 from, to 가 변경되어있기 때문에 따로 로직은 필요없음.
                //=> 바뀌어야 함.
                //신규 릴레이션인 경우에는 릴레이션 생성
                var edgeElement, originalData;
                var isComponent = false;
                if (edge.shape) {
                    edgeElement = edge;
                } else {
                    isComponent = true;
                    edgeElement = edge.element;
                }
                // console.log(from, to)

                if (edgeElement && from && to) {
                    var vertices = '[' + edgeElement.shape.geom.vertices.toString() + ']';
                    var componentInfo = {
                        component: 'class-relation',
                        sourceElement: from.$parent,
                        targetElement: to.$parent,
                        vertices: vertices,
                        isFilled: true,
                        relationView: {
                            style: JSON.stringify({}),
                            value: vertices,
                        }
                    }

                    from.$parent.value.elementView.id = from.id;
                    to.$parent.value.elementView.id = to.id;

                    if (isComponent) {
                        me.canvas.removeShape(edgeElement, true);
                        //this.removeComponentByOpenGraphComponentId(edgeElement.id);
                        //기존 컴포넌트가 있는 경우 originalData 와 함께 생성
                        this.addElement(componentInfo)
                    } else {
                        me.canvas.removeShape(edgeElement, true);
                        //기존 컴포넌트가 없는 경우 신규 생성
                        this.addElement(componentInfo);
                    }
                    // this.syncOthers();
                }
            }
            ,

            makeElement(ele, idx) {
                var me = this;
                var component
                var check = true

                if (ele._type == "org.uengine.modeling.model.Event") {
                    component = me.getComponentByName('domain-event-definition');
                } else if (ele._type == "org.uengine.modeling.model.BoundedContext") {
                    component = me.getComponentByName('bounded-context-definition');
                } else if (ele._type == "org.uengine.modeling.model.Aggregate") {
                    component = me.getComponentByName('aggregate-definition');
                } else if (ele._type == "org.uengine.modeling.model.Command") {
                    component = me.getComponentByName('command-definition');
                } else if (ele._type == "org.uengine.modeling.model.Policy") {
                    component = me.getComponentByName('policy-definition');
                } else if (ele._type == "org.uengine.modeling.model.View") {
                    component = me.getComponentByName('view-definition');
                } else if (ele._type == "org.uengine.modeling.model.External") {
                    component = me.getComponentByName('external-definition');
                } else if (ele._type == "org.uengine.modeling.model.Relation") {
                    component = me.getComponentByName('class-relation');
                }

                if (ele._type == "org.uengine.modeling.model.Relation") {
                    check = false
                } else {
                    check = true
                }


                if (check) {
                    var element = component.computed.createNew(
                        ele.elementView.id,
                        ele.elementView.x,
                        ele.elementView.y,
                        ele.elementView.width,
                        ele.elementView.height
                    )

                    // element.name = "tmp";
                    // element.loadData = true;
                    if (component.name == 'bounded-context-definition') {
                        element.aggregates = ele.aggregates
                        element.policies = ele.policies
                    } else if (component.name == 'aggregate-definition') {
                        element.aggregateRoot = ele.aggregateRoot
                        element.boundedContext = ele.boundedContext
                        if (ele.events) {
                            element.events = ele.events
                        } else {
                            element.events = []
                        }
                        if (ele.commands) {
                            element.commands = ele.commands
                        } else {
                            element.commands = []
                        }
                        if (ele.viewes) {
                            element.viewes = ele.viewes
                        } else {
                            element.viewes = []
                        }
                    } else if (component.name == 'domain-event-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = pluralize(ele.aggregate.name);
                        }
                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        // element.eventToPolicy = ele.eventToPolicy

                        element.relationPolicyInfo = ele.relationPolicyInfo
                        element.relationCommandInfo = ele.relationCommandInfo

                        element.fieldDescriptors = ele.fieldDescriptors
                        element.trigger = ele.trigger
                    } else if (component.name == 'command-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        }

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.restfulType = ele.restfulType
                    } else if (component.name == 'policy-definition') {
                        // if (ele.aggregate) {
                        //     ele.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                        //     ele.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                        //     ele.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        // }

                        // element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        // element.eventToPolicy = ele.eventToPolicy
                        element.relationEventInfo = ele.relationEventInfo
                    } else if (component.name == 'view-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        }

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.dataProjection = ele.dataProjection
                        element.aggregateEvents = ele.aggregateEvents

                        if (ele.dataProjection == 'cqrs') {
                            console.log(ele)
                            element.viewFieldDescriptors = ele.viewFieldDescriptors
                        } else {
                            // element.fieldDescriptorsMash = ele.viewFieldDescriptors
                        }
                    }

                    // element.portGenerated = ele.portGenerated
                    if (idx != null) {
                        // me.isUndoRedo = true
                        me.value['definition'].splice(idx, 1, element)
                        // me.value['definition'][idx].replace(me.value['definition'][idx],element)
                    } else {
                        me.value['definition'].push(element);
                    }

                } else {
                    var element = component.computed.createNew(
                        ele.relationView.id,
                        ele.sourceElement,
                        ele.targetElement,
                        ele.relationView.value,
                    )

                    if (component.name == 'class-relation') {
                        element.name = ele.name
                        element.from = ele.from
                        element.to = ele.to
                        element.relationView = ele.relationView
                    }

                    if (idx != null) {
                        me.value['relation'].splice(idx, 1, element)
                    } else {
                        me.value['relation'].push(element);
                    }
                }

                // element.drawer = true;

                me.$nextTick(function () {
                    element.name = ele.name;
                    // element.drawer = false;
                })

                return element
            },
            loadAddElement: function (value) {
                var me = this;

                // // console.log(JSON.parse(JSON.stringify(value)))
                me.value.definition = [];
                me.value.relation = [];
                me.projectName = value.projectName
                var boundedComponent = me.getComponentByName('bounded-context-definition');
                var aggregateComponent = me.getComponentByName('aggregate-definition');
                var commandComponent = me.getComponentByName('command-definition');
                var eventComponent = me.getComponentByName('domain-event-definition');
                var policyComponent = me.getComponentByName('policy-definition');
                var viewComponent = me.getComponentByName('view-definition');
                var relationComponent = me.getComponentByName('class-relation');

                if (value.boundedContexts)
                    value.boundedContexts.forEach(function (bounded, boundedIdx) {
                        // // console.log(bounded)
                        if (value.boundedContexts[boundedIdx].elementView.id == bounded.elementView.id) {
                            var ele = me.makeElement(bounded)
                            value.boundedContexts[boundedIdx] = ele
                            bounded.aggregates.forEach(function (agg, aggIdx) {
                                // // console.log(agg)

                                if (bounded.aggregates[aggIdx].elementView.id == agg.elementView.id) {
                                    var aggEle = me.makeElement(agg)
                                    bounded.aggregates[aggIdx] = aggEle
                                }
                                if (agg.events) {
                                    agg.events.forEach(function (ev, idx) {
                                        if (agg.events[idx].elementView.id == ev.elementView.id) {
                                            var ele = me.makeElement(ev)
                                            agg.events[idx] = ele
                                        }
                                    })
                                }
                                if (agg.commands) {
                                    agg.commands.forEach(function (co, idx) {
                                        if (agg.commands[idx].elementView.id == co.elementView.id) {
                                            var ele = me.makeElement(co)
                                            agg.commands[idx] = ele
                                        }
                                    })
                                }

                                if (agg.viewes) {
                                    agg.viewes.forEach(function (vi, idx) {
                                        if (agg.viewes[idx].elementView.id == vi.elementView.id) {
                                            var ele = me.makeElement(vi)
                                            agg.viewes[idx] = ele
                                        }
                                    })
                                }
                            })
                            if (bounded.policies) {
                                bounded.policies.forEach(function (po, idx) {
                                    if (bounded.policies[idx].elementView.id == po.elementView.id) {
                                        var ele = me.makeElement(po)
                                        bounded.policies[idx] = ele
                                    }

                                })
                            }
                        }
                    })

                if (value.relations)
                    value.relations.forEach(function (line) {
                        me.makeElement(line)
                    })

            },
            addElement: function (componentInfo, bounded) {
                this.enableHistoryAdd = true;
                var me = this;
                var additionalData = {};
                var vueComponent = me.getComponentByName(componentInfo.component);
                var element;
                // console.log(bounded)
                if (componentInfo.component == 'class-relation') {
                    //relation info setting before make
                    element = vueComponent.computed.createNew(
                        this.uuid(),
                        componentInfo.sourceElement.value,
                        componentInfo.targetElement.value,
                        componentInfo.vertices,
                    );

                    if (componentInfo.targetElement.value._type == 'org.uengine.modeling.model.Policy') {
                        element.name = 'Pub/Sub'

                        var checked = false
                        element.sourceElement.relationPolicyInfo.some(function (policyItem, evtIdx) {
                            var evObj = {
                                relationId: element.relationView.id,
                                policyValue: JSON.parse(JSON.stringify(element.targetElement))
                            }
                            if (policyItem.policyValue.elementView.id == element.targetElement.elementView.id) {
                                element.sourceElement.relationPolicyInfo[evtIdx] = evObj;
                                checked = true;
                            }
                        })

                        if (!checked) {
                            var evObj = {
                                relationId: element.relationView.id,
                                policyValue: JSON.parse(JSON.stringify(element.targetElement))
                            }
                            element.sourceElement.relationPolicyInfo.push(evObj)
                        }


                        //target setting
                        var checkedTwo = false
                        element.targetElement.relationEventInfo.forEach(function (eventItem, evtIdx) {
                            var evObj = {
                                relationId: element.relationView.id,
                                eventValue: JSON.parse(JSON.stringify(element.sourceElement))
                            }
                            if (eventItem.eventValue.elementView.id == element.sourceElement.elementView.id) {
                                element.targetElement.relationEventInfo[evtIdx] = evObj;
                                checkedTwo = true;
                            }
                        })

                        if (!checkedTwo) {
                            var evObj = {
                                relationId: element.relationView.id,
                                eventValue: JSON.parse(JSON.stringify(element.sourceElement))
                            }
                            element.targetElement.relationEventInfo.push(evObj)
                        }


                    } else if (componentInfo.targetElement.value._type == 'org.uengine.modeling.model.Command') {
                        element.name = 'Req/Res'

                        var checked = false
                        element.sourceElement.relationCommandInfo.some(function (policyItem, evtIdx) {
                            var evObj = {
                                relationId: element.relationView.id,
                                commandValue: JSON.parse(JSON.stringify(element.targetElement))
                            }
                            if (policyItem.commandValue.elementView.id == element.targetElement.elementView.id) {
                                element.sourceElement.relationCommandInfo[evtIdx] = evObj;
                                checked = true;
                            }
                        })

                        if (!checked) {
                            var evObj = {
                                relationId: element.relationView.id,
                                commandValue: JSON.parse(JSON.stringify(element.targetElement))
                            }
                            element.sourceElement.relationCommandInfo.push(evObj)
                        }

                        //target setting
                        var checkedTwo = false
                        element.targetElement.relationEventInfo.some(function (eventItem, evtIdx) {
                            var evObj = {
                                relationId: element.relationView.id,
                                eventValue: JSON.parse(JSON.stringify(element.sourceElement))
                            }
                            if (eventItem.commandValue.elementView.id == element.sourceElement.elementView.id) {
                                element.targetElement.relationEventInfo[evtIdx] = evObj;
                                checkedTwo = true;
                            }
                        })

                        if (!checkedTwo) {
                            var evObj = {
                                relationId: element.relationView.id,
                                eventValue: JSON.parse(JSON.stringify(element.sourceElement))
                            }
                            element.targetElement.relationEventInfo.push(evObj)

                        }

                    }

                } else {

                    element = vueComponent.computed.createNew(
                        this.uuid(),
                        componentInfo.x,
                        componentInfo.y,
                        componentInfo.width,
                        componentInfo.height
                    );

                }

                if (me.value == null) {
                    me.value = {
                        'definition': [],
                        'relation': []
                    }
                }

                if (element._type == 'org.uengine.modeling.model.Relation') {

                    if (bounded != undefined) {
                        if (bounded) {
                            console.log("bounded")
                            return element
                        }
                    } else {
                        me.value['relation'].push(element);
                        console.log("============== Storage Location Search Test 5-1 (Add Relation) ============= ")
                        this.$EventBus.$emit('storage')
                    }
                } else {
                    if (bounded != undefined) {
                        if (bounded) {
                            console.log("bounded")
                            return element
                        }
                    } else {
                        me.value['definition'].push(element);
                        // me.value.definition[element.elementView.id].push(element)
                        console.log("============== Storage Location Search Test 5-2 (Add Definition) ============= ")
                        this.$EventBus.$emit('storage')
                    }
                }
            },
            getComponentByName: function (name) {
                var componentByName;
                $.each(window.Vue._components, function (i, component) {
                    if (component.name == name) {
                        componentByName = component;
                    }
                });
                return componentByName;
            },
            getComponentByClassName: function (className) {
                var componentByClassName;

                $.each(window.Vue.classModelingComponents, function (i, component) {
                    if (component.default.computed && component.default.computed.className && component.default.computed.className() == className) {
                        componentByClassName = component.default;
                    }
                });
                return componentByClassName;
            },
            recursiveDeepCopy(o) {
                var me = this
                var newO,
                    i;

                if (typeof o !== 'object') {
                    return o;
                }
                if (!o) {
                    return o;
                }

                if ('[object Array]' === Object.prototype.toString.apply(o)) {
                    newO = [];
                    for (i = 0; i < o.length; i += 1) {
                        newO[i] = me.recursiveDeepCopy(o[i]);
                    }
                    return newO;
                }

                newO = {};
                for (i in o) {
                    if (o.hasOwnProperty(i)) {
                        newO[i] = me.recursiveDeepCopy(o[i]);
                    }
                }
                return newO;
            },
            getBoundedSetting(boundedId) {
                //USE CodeView
                var me = this
                return new Promise(async function (resolve, reject) {
                    let tmpList = []
                    var fullPathList = []
                    var getCodeList = []
                    var treeList = []
                    me.model = {
                        _type: "org.uengine.modeling.model.Model",
                        boundedContexts: [],
                        projectName: me.projectName
                    }

                    // return new Promise(async function (resolve, reject) {

                    // })

//
                    var copyValue = JSON.parse(JSON.stringify(me.value))

                    //boundedContexts 넣기
                    copyValue.definition.forEach(function (item) {
                        if (item != null) {
                            if (item.elementView.id == boundedId) {
                                me.model.boundedContexts.push(item)
                            }
                        }
                    })

                    var modelForElements = {
                        BoundedContext: [],
                        AggregateRoot: [],
                        Aggregate: [],
                        Policy: [],
                        Event: [],
                        Command: [],
                        View: [],
                        RelationEventInfo: [],
                        ViewEventInfo: [],
                    };
                    me.model.boundedContexts.forEach(function (bound, idx) {
                        bound.portGenerated = 8081 + idx
                        modelForElements["BoundedContext"].push(bound);
                        bound.groups.forEach(function (groupsId) {
                            if (me.$refs[groupsId][0].value._type == 'org.uengine.modeling.model.Event') {
                                if (me.$refs[groupsId][0].value.aggregate.name == undefined) {
                                    var tempEvent = me.$refs[groupsId][0].value
                                    modelForElements['Event'].push(tempEvent)
                                }

                            }
                        })
                        if (me.model.boundedContexts[idx].aggregates.length >= 1) {
                            me.model.boundedContexts[idx].aggregates.forEach(function (agg, aggIdx) {
                                var lifeCycles = [
                                    {trigger: 'PostPersist', annotation: '@PostPersist', events: []},
                                    {trigger: 'PostUpdate', annotation: '@PostUpdate', events: []},
                                    {trigger: 'PostDelete', annotation: '@PostDelete', events: []},
                                    {trigger: 'PrePersist', annotation: '@PrePersist', events: []},
                                    {trigger: 'PreUpdate', annotation: '@PreUpdate', events: []},
                                    {trigger: 'PreDelete', annotation: '@PreDelete', events: []},
                                ]
                                // fieldDescriptors 전처리
                                agg.aggregateRoot.fieldDescriptors.forEach(function (fd) {
                                    fd.nameCamelCase = _.camelCase(fd.name)
                                    fd.namePascalCase = fd.nameCamelCase.substring(0, 1).toUpperCase() + fd.nameCamelCase.substring(1);
                                    if (fd.isKey)
                                        agg.aggregateRoot.keyFieldDescriptor = fd;
                                })
                                modelForElements["Aggregate"].push(agg);
                                modelForElements["AggregateRoot"].push(agg.aggregateRoot);
                                if (agg.commands)
                                    agg.commands.forEach(function (co) {
                                        if (co.name == '') {
                                            me.text = "Check Command Name"
                                            me.snackbar = true
                                            me.color = "error"
                                            me.timeout = 1000
                                            me.checkName = false
                                        }
                                        modelForElements["Command"].push(co);
                                    })

                                if (agg.events)
                                    agg.events.forEach(function (ev) {
                                        lifeCycles.some(function (lifeCycle) {
                                            if (lifeCycle.annotation == ev.trigger) {
                                                lifeCycle.events.push(ev)
                                                return;
                                            }
                                        })
                                        if (ev.name == '') {
                                            me.text = "Check Event Name"
                                            me.snackbar = true
                                            me.timeout = 1000
                                            me.color = "error"
                                            me.checkName = false
                                        }
                                        modelForElements["Event"].push(ev);
                                    })

                                lifeCycles.forEach(function (lifeCycleNull, idx) {
                                    if (lifeCycleNull.events.length == 0) {
                                        lifeCycles[idx] = null;
                                    }
                                })

                                lifeCycles = lifeCycles.filter(n => n);

                                if (lifeCycles.length > 0) {
                                    console.log(lifeCycles)
                                    me.model.boundedContexts[idx].aggregates[aggIdx].lifeCycles = lifeCycles;
                                }
                            })
                        }

                        if (me.model.boundedContexts[idx].policies.length >= 1) {
                            me.model.boundedContexts[idx].policies.forEach(function (po) {
                                if (po.name == '') {
                                    me.text = "Check Policy Name"
                                    me.snackbar = true
                                    me.timeout = 1000
                                    me.color = "error"

                                    me.checkName = false
                                }
                                // console.log(po)
                                po.relationEventInfo.forEach(function (item) {
                                    // console.log(item)
                                    item.boundedContext = {}
                                    item.boundedContext.name = po.boundedContext.name
                                    item.elementView = {}
                                    item.elementView.id = po.elementView.id
                                    // item.eventValue.boundedContext.name = po.boundedContext.name
                                    modelForElements["RelationEventInfo"].push(item)
                                })
                                modelForElements["Policy"].push(po);
                            })
                        }

                        if (me.model.boundedContexts[idx].viewes.length >= 1) {
                            me.model.boundedContexts[idx].viewes.forEach(function (vi) {
                                if (vi.name == '') {
                                    me.text = "Check View Name"
                                    me.snackbar = true
                                    me.timeout = 1000
                                    me.color = "error"

                                    me.checkName = false
                                }


                                vi.createRules.forEach(function (fd, index) {

                                    if (Object.keys(fd.when).length == 0) {
                                        vi.createRules[index] = null
                                        vi.createRules = vi.createRules.filter(n => n)
                                    } else {
                                        fd.when.boundedContext.name = vi.boundedContext.name;
                                        modelForElements["ViewEventInfo"].push(fd.when)
                                        fd.fieldMapping.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.fieldMapping[idx] = null
                                                fd.fieldMapping = fd.fieldMapping.filter(n => n)
                                            }
                                        })

                                        fd._index = index + 1
                                    }

                                })

                                vi.updateRules.forEach(function (fd, index) {
                                    if (Object.keys(fd.when).length == 0) {
                                        vi.updateRules[index] = null
                                        vi.updateRules = vi.updateRules.filter(n => n)
                                    } else {
                                        fd.when.boundedContext.name = vi.boundedContext.name;
                                        modelForElements["ViewEventInfo"].push(fd.when)
                                        fd.fieldMapping.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.fieldMapping[idx] = null
                                                fd.fieldMapping = fd.fieldMapping.filter(n => n)
                                            }
                                        })
                                        fd.where.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.where[idx] = null
                                                fd.where = fd.where.filter(n => n)
                                            }
                                        })
                                        fd._index = index + 1
                                    }

                                })
                                vi.deleteRules.forEach(function (fd, index) {
                                    if (Object.keys(fd.when).length == 0) {
                                        vi.deleteRules[index] = null
                                        vi.deleteRules = vi.deleteRules.filter(n => n)
                                    } else {
                                        fd.when.boundedContext.name = vi.boundedContext.name;
                                        modelForElements["ViewEventInfo"].push(fd.when)
                                        fd.where.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.where[idx] = null
                                                fd.where = fd.where.filter(n => n)
                                            }
                                        })
                                        fd._index = index + 1
                                    }
                                })

                                modelForElements["View"].push(vi);
                            })
                        }

                    })

                    //template Setting
                    if (me.template.length > 0) {
                        var template = me.template;
                    } else {
                        var template = 'spring-boot';
                    }

                    // 아래 데이터는 npm 빌드때 파일시스템 tree 탐색을 통해 자동으로 생성하거나 일일이 작업해줘야 한다.
                    var lastIndex = me.$manifestsPerTemplate[template].length - 1


                    me.$manifestsPerTemplate[template].forEach(function (element, index) {
                        element = element.replace('./', '')
                        var doit = function (content) {
                            if (element.includes('for-model')) {
                            }
                            var test = ''
                            var count = 0
                            var optionsCheck = content.split('---')
                            if (!content.includes('forEach:')) {
                                count = count + 1
                                if (element.includes('for-model')) {
                                    var forEach = 'Model'
                                    test = test.concat('forEach: ' + forEach)
                                } else {
                                    var forEach = 'BoundedContext'
                                    test = test.concat('forEach: ' + forEach)
                                }
                            } else {
                                var forEach = content.split("forEach:")[1].split("\n")[0].trim();
                            }

                            if (!content.includes('fileName:')) {
                                count = count + 1
                                if (element.split('/')) {
                                    var fileName = element.substring(element.lastIndexOf("/") + 1);
                                    test = test.concat('\n')
                                    test = test.concat('fileName: ' + fileName)
                                }
                            }
                            if (!content.includes('path:')) {
                                var pathData = element.substring(0, element.lastIndexOf("/") + 1);
                                count = count + 1
                                if (element.includes('for-model')) {
                                    test = test.concat('\n')
                                    test = test.concat(`path: ${path.dirname(element).replace('./', '')}`)
                                } else {
                                    test = test.concat('\n')
                                    test = test.concat(`path: {{name}}/${path.dirname(element).replace('./', '')}`)
                                }
                            } else if (!content.includes('forEach:')) {
                                var pathData = element.substring(0, element.lastIndexOf("/") + 1);
                                if (element.includes('for-model')) {
                                    test = test.concat('\n')
                                } else if (content.includes(' path:')) {
                                    count = count + 1
                                    test = test.concat('\n')
                                    test = test.concat(`path: {{name}}/${path.dirname(element).replace('./', '')}`)
                                } else {
                                    test = test.concat('\n')
                                }
                            }
                            if (count == 3) {
                                test = test.concat('\n---\n')
                            } else {
                                test = test.concat('\n')
                            }

                            if (test.length > 0) {
                                content = test.concat(content)
                            }
                            var model = modelForElements[forEach];

                            // if(model[0]._type == 'org.uengine.modeling.model.Policy' && model[0].relationEventInfo.length > 0)

                            var process = function (ele) {
                                if (ele == undefined) {
                                    var optionsMetadataAndSource = content.split("---"); // 소스와 구분
                                    var optionsPathHeader = yamlpaser.load(optionsMetadataAndSource[0])

                                    var elementPath = element.split('/');
                                    var elementPathString = elementPath[0] + '/' + elementPath[1];
                                    if (!optionsPathHeader['path'].includes(elementPathString)) {
                                        optionsPathHeader['path'] = elementPathString.concat(optionsPathHeader['path'])
                                    }

                                    var optionsPath = optionsPathHeader['path'].split('/');
                                    var optionsPathString = '';
                                    for (var o = 1; o < optionsPath.length; o++) {
                                        if (o == 1) {
                                            optionsPathString = optionsPathString.concat(optionsPath[o]);
                                        } else {
                                            optionsPathString = optionsPathString.concat('/' + optionsPath[o]);
                                        }
                                    }
                                    //gateway2/src/main/java/com/example/template
                                    //gateway/src/main/java/com/example

                                    // FullPath : for-model/gateway/src/main/java/com/example/template/Application.java
                                    // Path: /com/example
                                    // if()
                                    var options = {
                                        "package": `${optionsPathString}`.replace(elementPath[1] + '/src/main/java/', '').replace(/\//gi, '.'),
                                        "packagePath": `${optionsPathString}`
                                    }

                                    ele = me.model

                                } else {
                                    var options = {
                                        "package": `${me.projectName}`,
                                        "packagePath": `src/main/java/${me.projectName.replace(".", "/")}`
                                    }
                                }

                                ele.options = options;

                                var gen = window.$Mustache.render(content, ele);
                                var metadataAndSource = gen.split("---"); // 소스와 구분
                                var header = yamlpaser.load(metadataAndSource[0])
                                var options = {}
                                Object.keys(header).forEach(function (keyValue, idx) {
                                    var key = keyValue
                                    var value = Object.values(header)[idx]
                                    if (key == 'path' && (value.lastIndexOf('.') == value.length - 1)) {
                                        value = value.substring(0, value.lastIndexOf(".") - 1)
                                    }
                                    options[key] = value;
                                })

                                var fileName = options['fileName']
                                var fullPath = options['path'] + '/' + options['fileName']
                                // console.log(fullPath)
                                if (fileName.includes('Application'))

                                    if (elementPathString && !fullPath.includes(elementPathString)) {
                                        fullPath = elementPathString.concat(fullPath)
                                    }
                                // /src/main/java/com/example/Application.java
                                // for-model/gateway2/src/main/java/com/example/template/Application.java
                                var source = ''

                                if (metadataAndSource.length > 1) {
                                    for (var data in metadataAndSource) {

                                        if (data != 0 && data < metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data] + '---\n')
                                        } else if (data == metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data])
                                        }
                                    }
                                }

                                var codeValue = {}
                                var fullValue = {}
                                source = source.replace('\n', '')
                                if (source.length > 1) {
                                    if (ele._type == 'org.uengine.modeling.model.Model') {
                                        // /src/main/java/com/example/Application.java
                                        // gateway2/src/main/java/com/example/template/Application.java

                                        codeValue = {
                                            'key': elementPath[1] + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': elementPath[1] + fileName,
                                            'fullPath': fullPath.replace('for-model/', '')
                                        }
                                    } else {
                                        codeValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fullPath': fullPath
                                        }
                                    }


                                    getCodeList.push(codeValue)
                                    fullPathList.push(fullValue)
                                }
                            }


                            if (Array.isArray(model)) {
                                model.forEach(process);
                            } else {
                                process(model);
                            }

                        } //end of doit


                        var xhttp = new XMLHttpRequest();
                        xhttp.onreadystatechange = function () {

                            if (this.readyState == 4 && this.status == 200) {
                                if (me.template == 'GIT') {
                                    var gitCodeObj = JSON.parse(this.responseText);
                                    var gitCodeTmp = Base64.decode(gitCodeObj.content)
                                    doit(gitCodeTmp)
                                } else {
                                    doit(this.responseText);
                                }
                                if (lastIndex == index) {
                                    var tmp = {
                                        'getCodeList': getCodeList,
                                        'fullPathList': fullPathList
                                    }
                                    resolve(tmp);
                                }
                            }
                        };
                        if (me.template == 'GIT') {
                            xhttp.open("GET", me.gitCodeUrl[element], true);
                            xhttp.send();
                        } else {
                            xhttp.open("GET", "/static/templates/" + template + "/" + element, true);
                            xhttp.send();
                        }


                    });

                })

            },
        }
    }
</script>

