                >
                    <v-img
                            :src="item.img"
                            max-width="400"
                            max-height="300"

                    ></v-img>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <v-list-item-title class="headline mb-1">{{ item.projectName }}</v-list-item-title>
                            <v-list-item-subtitle>Author : {{ item.email }}</v-list-item-subtitle>
                            <v-list-item-subtitle>Modified Date : {{ item.date }}</v-list-item-subtitle>
                            <v-list-item-subtitle>Comment : {{item.comment}}</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <!--                        toUrl(projectName, author, verName, type) -->
                        <v-btn text @click.native="$EventBus.$emit('chatKey', item.author)"
                               v-if="isYours(item.author)"
                               :to="toUrl(
                               item.author,
                               item.projectId,
                               item.projectVersion,
                               item.type)">
                            Edit
                        </v-btn>

                        <v-btn text @click.native="$EventBus.$emit('chatKey', item.author)"
                               v-if="!isYours(item.author)"
                               :to="toUrl(
                               item.author,
                               item.projectId,
                               item.projectVersion,
                               item.type)">
                            View
                        </v-btn>

                        <v-btn
                                v-if="isYours(item.author)"
                                color="red"
                                text
                                @click="deleteConfirm(item)"
                        >
                            Delete
                        </v-btn>
                    </v-card-actions>
                </v-card>

            </v-col>
            <v-col
                    cols="6"
                    md="4"
            >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        align="center"
                >
                </v-card>
            </v-col>
        </v-row>


        <v-dialog v-model="deleteDialog" persistent max-width="370">
            <v-card>
                <v-card-title class="headline" v-if="selectDelete.type == true">삭제 알림 ( 공유 )</v-card-title>
                <v-card-title class="headline" v-else>삭제 알림( 개인 )</v-card-title>
                <v-card-text>
                    <div style="color: #8b0000; font-size: medium">현재 프로젝트 "{{selectDelete.name}}" 를 삭제 하시겠습니까?</div>
                    <div>사용자 : {{selectDelete.author}}</div>
                    <div>최근 저장시간 : {{selectDelete.date}}</div>
                    <div>삭제 후에는 복구는 불가능합니다.</div>
                    <v-checkbox label="동의 하였으며, 삭제를 진행합니다." v-model="deleteConfirmCheckBox"></v-checkbox>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click.native="deleteItem">Delete</v-btn>
                    <v-btn color="green darken-1" text @click.native="deleteDialog = false">Close</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="newProjectDialog" persistent max-width="430">
            <v-card>
                <v-card-title class="headline">삭제 알림</v-card-title>
                <v-card-text>
                    <div style="color: #8b0000; font-size: medium">편집중인 로컬 문서( {{loginProjectName}} )가 존재합니다.</div>
                    <div style="color: #000000">로컬에 저장된 문서를 편집하시겠습니까, 새 문서를 만드시겠습니까?</div>
                    <div style="color: #000000">새 문서로 만드는 경우는 기존 로컬문서가 상실됩니다.</div>
                    <v-checkbox label="동의 하였으며, 새 문서를 생성합니다." v-model="deleteConfirmCheckBox"></v-checkbox>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click.native="createNewProject()">새 프로젝트</v-btn>
                    <v-btn color="blue darken-1" text :to="toUrl(loginAuthor,'init', '-init', 0)">
                        현재 프로젝트
                    </v-btn>
                    <v-btn color="green darken-1" text @click="newProjectDialog = false"> 닫기</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <!--        <v-dialog v-model="saveDialog" persistent max-width="300">-->
        <!--            <v-card>-->
        <!--                <v-card-title class="headline">저장 정보</v-card-title>-->
