        <!--                    <v-btn color="green darken-1" text @click.native="saveDialog = false"> 닫기</v-btn>-->
        <!--                </v-card-actions>-->
        <!--            </v-card>-->
        <!--        </v-dialog>-->

    </v-container>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "EventStormingListPage",
        data() {
            return {
                stormingLocalList: [],
                stormingSharedList: [],
                stormingMineList: [],
                //user info
                loginProjectName: '',
                loginAuthor: '',
                // loginProjectId: 'init',
                // loginProjectVersion: '-init',
                //search
                search: '',
                //delete info
                deleteDialog: false,
                selectDelete: [],
                //newProject
                newProjectDialog: false,
                deleteConfirmCheckBox: false,
                loadingDialog: true,
                //server Check도
                // saveDialog: false,
                // saveMine: false,
            }
        },
        created() {
            var me = this;
            me.newProjectDialog = false
            // me.$EventBus.$on('showSave', function () {
            //     me.saveDialog = true
            // })
            // me.$EventBus.$on('saveComplete', function () {
            //     me.saveMine = true
            // })
            me.$EventBus.$emit('progressValue', true)
            if (window.localStorage.getItem("accessToken") != null) {
                me.getList().then(function () {
                    me.filteredLocalList
                    me.filteredMineList
                    me.filteredSharedList
                    me.$EventBus.$emit('progressValue', false)
                })


            } else {
                me.getList().then(function () {
                    me.filteredLocalList
                    me.$EventBus.$emit('progressValue', false)
                })

            }
            //new Project
            me.loginAuthor = localStorage.getItem("uid") != null ? localStorage.getItem("uid") : "anonymous"
            // me.loginProjectId = localStorage.getItem("uid") != null ? localStorage.getItem("uid") : "anonymous"
            // me.loginProjectVersion = localStorage.getItem("uid") != null ? localStorage.getItem("uid") : "anonymous"
            me.loginProjectName = localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local'

        },
        computed: {
            filteredLocalList() {
                var me = this
                var filter = []
                for (var name in me.stormingLocalList) {
                    var ob = {
                        'email': me.stormingLocalList[name].author,
                        'date': me.stormingLocalList[name].date,
                        'img': me.stormingLocalList[name].img,
                        // 'name': me.stormingLocalList[name].name,
                        // 'version': me.stormingLocalList[name].version,
                        'userUid': me.stormingLocalList[name].userUid,
                        'projectId': me.stormingLocalList[name].projectId,
                        'projectVersion': me.stormingLocalList[name].projectVersion,
                        'projectName': me.stormingLocalList[name].projectName,
                        'type': me.stormingLocalList[name].type,
                    }

                    // console.log(ob)
                    filter.push(ob)
                }

                return filter
            },
            filteredMineList() {
                var me = this
                var filter = []
                for (var name in me.stormingMineList) {
                    var ob = {
                        'projectId': name,
                        'projectVersion': Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1],
                        'projectName': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].projectName,
                        'img': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].img,
                        'date': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].date,
                        'email': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].email,
                        'author': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].author,
                        'userName': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].userName,
                        'comment': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].comment,
                        'type': false,

                    }
                    filter.push(ob)
                }

                if (me.search) {
                    return filter.filter(eventstorming => {
                        return eventstorming.name.toLowerCase().includes(me.search.toLowerCase())
                    })
                } else {

                    return filter
                }

            },
            filteredSharedList() {
                var me = this
                var filter = [];
                for (var uid in me.stormingSharedList) {
                    for (var name in me.stormingSharedList[uid]) {
                        var ob = {
                            'author': uid,
                            'projectId': name,
                            'projectVersion': Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1],
                            'projectName': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].projectName,
                            'img': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].img,
                            'date': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].date,
                            'email': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].email,
                            'userName': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].userName,
                            'comment': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].comment,
                            'type': true
                        }
                        // console.log(ob)
                        filter.push(ob)
                    }
                }


                if (me.search) {
                    return filter.filter(eventstorming => {
                        return eventstorming.name.toLowerCase().includes(me.search.toLowerCase())
                    })
                } else {
                    return filter
                }

            }

        },
        watch: {},
        mounted: function () {
            var me = this

            //login toaken info
            me.$EventBus.$on('login', function (newVal) {
                me.getList()
            })

            me.$EventBus.$on('updatePic', function (newVal) {
                me.getList().then(function () {
                    me.filteredLocalList
                })
            })
            //
            // me.$EventBus.$on('saving' , function (newVal) {
            //     console.log(newVal)
            // })
            //
        },
        beforeDestroy() {
            this.$EventBus.$off('updatePic')
        },
        methods: {
            createNewProject() {
                var me = this
                if (me.deleteConfirmCheckBox) {
                    localStorage.removeItem('localLoadData')
                    localStorage.removeItem('localSaveImg')
                    localStorage.removeItem('localSaveProjectName')
                    me.deleteConfirmCheckBox = false
                    me.$router.push({path: `${me.toUrl(me.loginAuthor,'init', '-init', 0)}`});
                } else {
                    alert("삭제를 원하시면 '☐' 체크 해주세요.")
                }
            },
            newProjectCheck() {
                var me = this

                if (!(JSON.parse(localStorage.getItem('localLoadData')) == null)) {
                    if (JSON.parse(localStorage.getItem('localLoadData'))['definition'].length == 0) {
                        // No data
                        me.$router.push({path: `${me.toUrl(me.loginAuthor, 'init', '-init', 0)}`});
                    } else {
                        // YES data
                        me.newProjectDialog = true;

                    }
                } else {
                    //No data
                    me.$router.push({path: `${me.toUrl(me.loginAuthor,'init', '-init', 0)}`});
                }
            },
            isYours(getUid) {

                if (localStorage.getItem("uid") == getUid) {
                    return true
                } else {
                    return false
                }

            },
            toUrl(author, projectId, projectVersion, type) {
                return "storming/" + author + "/" + projectId + "/" + projectVersion + "/" + type
            },
            deleteConfirm(item) {
                var me = this

                me.selectDelete = JSON.parse(JSON.stringify(item))
                me.deleteDialog = true
            },
            deleteItem() {
                var me = this;
                var deleteItem;

                if (me.deleteConfirmCheckBox) {

                    //
                    if (me.selectDelete.type) {
                        deleteItem = firebase.database().ref('/saver/shared/' + me.selectDelete.author + "/" + me.selectDelete.projectId)
                    } else {
                        deleteItem = firebase.database().ref('/saver/mine/' + me.selectDelete.author + "/" + me.selectDelete.projectId)
                        deleteItem = firebase.database().ref('/saver/versionList/' + me.selectDelete.author + "/" + me.selectDelete.projectId)
                    }

                    deleteItem.remove()

                    me.$nextTick(function () {
                        me.getList()
                        me.deleteDialog = false
                    })

                } else {
                    alert("삭제를 원하시면 '☐' 체크 해주세요.")
                }

            },
            getUid() {
                return new Promise(function (resolve) {
                    firebase.auth().onAuthStateChanged(function (user) {
                        if (user) {
                            resolve(user.uid);
                        } else {
                            resolve("everyone")
                        }
                    });
                })
            },
            getList() {
                var me = this

                return new Promise(function (resolve) {

                    var userId = localStorage.getItem("uid")
                    var local = [
                        {
                            // version: '0',
                            // name: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local',
                            author: localStorage.getItem("email") != null ? localStorage.getItem("email") : "anonymous@sample.org",
                            date: localStorage.getItem("localSaveTime") != null ? localStorage.getItem("localSaveTime") : "undefined",
                            img: localStorage.getItem('localSaveImg') != null ? localStorage.getItem('localSaveImg') : 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                            userUid: userId != null ? userId : "anonymous",
                            projectId: 'init',
                            projectVersion: '-init',
                            projectName: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local',
                            type: 0,

                        },
                        // {
                        //     author: "anonymous@sample.org",
                        //     date: "undefined",
                        //     img: 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                        //     userUid: "anonymous",
                        //     projectId: 'multiTest',
                        //     projectVersion: '-init',
                        //     projectName: 'multiTest',
                        //     type: 1,
                        // },
                        {
                            author: "anonymous@sample.org",
                            date: "undefined",
                            img: 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                            userUid: "anonymous",
                            projectId: 'multiTest2',
                            projectVersion: '-init',
                            projectName: 'multiTest2',
                            type: 2,
                        }
                    ]

                    if (userId) {
                        me.stormingLocalList = local

                        firebase.database().ref('/saver/mine/' + userId).once('value').then(function (snapshot) {
                            me.stormingMineList = snapshot.val();
                            resolve()
                        });
                        firebase.database().ref('/saver/shared/').once('value').then(function (snapshot) {
                            me.stormingSharedList = snapshot.val();
                            resolve()
                        });
                    } else {
                        me.stormingLocalList = local
                        resolve()
                    }

                })


            }
        }
    }
</script>

